import { SupportedLanguages } from 'models/languages';

export const getLanguageToggleLabel = (currentLanguage: SupportedLanguages) => {
  return currentLanguage === 'ko' ? 'EN' : 'KR';
};

export const getLanguageTogglePath = (currentLanguage: SupportedLanguages) => {
  return currentLanguage === 'ko' ? '/en/' : '/';
};
