import { SupportedLanguages } from 'models/languages';

export interface Menu {
  name: string;
  path: string;
}

const menuNames: Record<SupportedLanguages, Record<string, string>> = {
  ko: { home: '', about: 'About' },
  en: { home: 'Home', about: 'About' },
};

export const getGlobalMenus = (lang: SupportedLanguages): Menu[] => [
  { name: menuNames[lang].home, path: lang === 'ko' ? '/' : '/en' },
  { name: menuNames[lang].about, path: lang === 'ko' ? '/about' : '/about/en/' },
];
