import React from 'react';
import { Link } from 'gatsby';
import { Box, Container, Grid, IconButton, useTheme } from '@mui/material';
import { getGlobalMenus } from 'models/menu';
import { useLanguage } from 'hooks/useLanguage';
import { useColorScheme } from '@mui/material/styles';
import * as Icon from 'react-feather';
import DrawerMenu from './DrawerMenu';
import { getLanguageToggleLabel, getLanguageTogglePath } from './languageToggle';

const Header = () => {
  const { spacing, palette, breakpoints, typography } = useTheme();
  const { language } = useLanguage();
  const { mode, setMode, systemMode } = useColorScheme();
  const globalMenus = getGlobalMenus(language);
  const toggleLanguagePath = getLanguageTogglePath(language);
  const toggleLanguageLabel = getLanguageToggleLabel(language);

  const currentMode = mode === 'system' ? (systemMode ?? 'light') : mode;
  const toggleThemeMode = () => {
    if (!mode) {
      return;
    }
    setMode(currentMode === 'dark' ? 'light' : 'dark');
  };

  const themeToggleButton = (
    <IconButton onClick={toggleThemeMode} size="small" aria-label="toggle dark mode" disabled={mode == null}>
      {currentMode === 'dark' ? <Icon.Sun size={16} /> : <Icon.Moon size={16} />}
    </IconButton>
  );

  return (
    <Container
      component="header"
      sx={[
        {
          position: 'fixed',
          backgroundColor: 'rgba(250, 248, 245, 0.85)',
          width: '100%',
          left: '50%',
          transform: 'translateX(-50%)',
          zIndex: 300,
          backdropFilter: 'blur(3px)',
          paddingTop: spacing(1),
          height: 48,
        },
        theme =>
          theme.applyStyles('dark', {
            backgroundColor: 'rgba(17, 17, 17, 0.78)',
          }),
      ]}
    >
      <Box position="relative" height="100%" display="flex">
        <Grid
          container
          alignItems="center"
          sx={{
            justifyContent: 'space-between',
          }}
        >
          <Grid>
            <Box
              component="span"
              sx={{
                [breakpoints.up('md')]: {
                  display: 'none',
                },
              }}
            >
              <DrawerMenu />
            </Box>
          </Grid>
          <Grid
            sx={{
              [breakpoints.up('sm')]: {
                display: 'none',
              },
            }}
          >
            {themeToggleButton}
          </Grid>
          <Grid
            component="ul"
            alignItems="center"
            xs={5}
            container
            sx={{
              [breakpoints.down('sm')]: {
                display: 'none',
              },
              margin: 0,
              padding: 0,
              justifyContent: 'flex-end',
              fontSize: typography.body2.fontSize,
              '& li': {
                margin: 0,
                listStyle: 'none',
                marginRight: spacing(3),
              },
              '& a': {
                textDecoration: 'none',
                color: 'inherit',
                '&.active': {
                  color: palette.primary.main,
                },
                '&:hover': {
                  textDecoration: 'underline',
                },
                '&:visited': {
                  color: 'inherit',
                },
              },
            }}
          >
            {globalMenus.map(menu => (
              <Grid component="li" item key={menu.path}>
                <Link to={menu.path}>{menu.name}</Link>
              </Grid>
            ))}
            <Grid component="li" item>
              <Link to={toggleLanguagePath}>{toggleLanguageLabel}</Link>
            </Grid>
            <Grid component="li" item sx={{ marginRight: 0 }}>
              {themeToggleButton}
            </Grid>
          </Grid>
        </Grid>
      </Box>
    </Container>
  );
};

export default Header;
